.container.video{
      position: relative;
      z-index: 2;
}

.video_title{
      margin-top: 5rem;
}

.video_title h3{
      width: 100%;
      text-align: center;
      color: var(--theme-400);
      font-size: 2rem;
}

.video_box{
      width: 40rem;
      height: 22.6rem;
      background: #222;
      margin-top: 3rem;
      position: relative;
}

.video_box:after{
      width: 75%;
      height: 3rem;
      border-radius: 50%;
      background: #000;
      left: 12.5%;
      bottom: -1rem;
      filter: blur(15px);
      opacity: .6;
      z-index: -1;
}

.video_box iframe{
      border: none;
}