.main_content{
      flex: 1;
      height: 100%;
      position: relative;
}

.content_header,
.main_header{
      width: 100%;
      height: 10.5rem;
      background: var(--light);
      border-radius: 0 3rem 0 0;
      position: relative
}

.content_header{
      position: relative;
      z-index: 10;
}

.main_header{
      padding: 3rem 3rem 3rem 1.5rem;
}

.main_header:before{
      width: 75%;
      height: 3rem;
      background: #000;
      border-radius: 50%;
      bottom: -1rem;
      left: 12.5%;
      filter: blur(1.5rem);
      z-index: -1;
      opacity: .1;
}

.wrapper_online{
      height: 6rem;
      display: flex;
      gap: .5rem;
}

.online_avatar{
      width: 6rem;
      height: 6rem;
      border-radius: 50%;
      position: relative;
      box-shadow: 0 0 0 .2rem var(--secundary) inset;
      padding: .5rem;
}

.online_avatar img{
      width: 5rem;
      height: 5rem;
      border-radius: 50%;
}

.online_dots{
      width: 1.4rem;
      height: 1.4rem;
      border-radius: 50%;
      background: var(--grys-300);
      position: absolute;
      top: 0;
      right: .5rem;
}

.online_dots.green{
      background: var(--green);
}

.online_dots.red{
      background: var(--red-100);
}

.online_data{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      color: var(--grys-300);
      font-family: var(--ft-1);
}

.online_data strong{
      color: var(--theme-400);
      font-size: 1.8rem;
      font-weight: 400;
      text-transform: uppercase;
      line-height: 1.8rem;
}

.online_data p{
      font-size: 1.4rem;
}

.online_data span{
      display: block;
      text-align: right;
      font-family: var(--ft-2);
      color: var(--secundary);
      font-weight: 700;
      font-size: 1.7rem;
      width: 100%;
}

.pages{
      background: var(--light);
      width: 100%;
      height: calc(100% - 10.5rem);
      position: relative;
      overflow: hidden;
      border-radius: 0 0 3rem 0;
}

.wrapper_page{
      position: relative;
      width: 100%;
      height: 100%;
      background: var(--light);
      padding: 3rem;
      left: 0;
      transform: translateX(-100%);
      opacity: 0;
      visibility: hidden;
}

.wrapper_page.show{
      animation: show_page .5s cubic-bezier(0,.45,.44,.98) forwards;
}

@keyframes show_page{
      100%{
            opacity: 1;
            visibility: visible;
            transform: translateX(0);
      }
}

.page_home__content{
      width: 100%;
      height: auto;
      display: flex;
      flex-direction: column;
}

.page_home__content h1{
      font-size: 3rem;
      line-height: 2.7rem;
      color: var(--theme-400);
}

.page_home_description{
      width: 100%;
      margin-top: 2rem;
      height: 17rem;
      overflow-x: hidden;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--alpha) var(--theme-400);
}

.page_home_description p{
      font-size: 1.6rem;
      color: var(--grys-300);
}

.friend_content{
      width: 38rem;
      height: 26rem;
      margin-left: calc(50% - 19rem);
}


.relation_content{
      width: 100%;
      margin-top: 2rem;
}

.relation_header{
      display: grid;
      grid-template-columns: 20rem 1fr;
      gap: 2rem;
}

.relation_avatar,
.relation_avatar img{
      width: 20rem;
      height: 20rem;
      border-radius: 2rem;
      display: flex;
}

.relation_avatar{
      box-shadow: 2rem 2rem 2rem rgba(0,0,0,.2);
}

.relation_data{
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
}

.relation_data strong{
      color: var(--theme-400);
      font-size: 4rem;
      font-family: var(--ft-1);
      position: relative;
      width: 100%;
}

.relation_data strong:before{
      width: 70%;
      height: .3rem;
      background: var(--theme-400);
      right: 0;
      bottom: 0;
}

.relation_data span{
      font-size: 3.5rem;
      color: var(--secundary);
      font-weight: 600;
      padding-left: 30%;
}

.relation_data a{
      text-transform: uppercase;
      background: var(--theme-400);
      width: fit-content;
      padding: .5rem 1.75rem;
      border-radius: 10rem;
      color: var(--light);
      font-size: 1.4rem;
}

.relation_about{
      margin-top: 3rem;
}

.relation_about p{
      color: var(--grys-300);
      font-size: 1.6rem;
}

.relation_footer{
      margin-top: 4rem;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 1rem;
}

.relation_footer *{
      color: var(--theme-400);
      font-size: 3rem;
}

.relation_footer h3{
      font-size: 2rem;
}

.container{
      width: 42rem;
      height: 59rem;
      overflow-x: hidden;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--alpha) var(--theme-400);
      margin-top: 2rem;
}

.wrapper_gallery{
      width: 100%;
      height: auto;
      display: flex;
      flex-direction: column;
      gap: 3rem;
}

.gallery_row{
      width: 99%;
      height: 38rem;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: .6rem;
      overflow: hidden;
}

.gallery_row img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.btn_open__modal{
      position: absolute;
      width: 7rem;
      height: 7rem;
      border-radius: 50%;
      background: rgba(0,0,0,.5);
      transition: .2s ease-in-out;
      display: flex;
      align-items: center;
      justify-content: center;
      transform: scale(1.4);
      opacity: 0;
}

.gallery_row:hover .btn_open__modal{
      transform: scale(1);
      opacity: 1;
}

.btn_open__modal *{
      color: var(--light);
      font-size: 2.2rem;
}