.title_content{
      position: relative;
      width: 100%;
      height: 2.6rem;
      display: flex;
      align-items: center;
      justify-content: center;
}

.title_content:before,
.title_content:after{
      width: 100%;
      height: .1rem;
      top: calc(50% - 0.05rem);
      background: var(--theme-400);
}

.title_content:after{
      width: 50%;
      top: 100%;
      left: 50%;
      transform: translate(-50%, -1rem);
      box-shadow: 0 -.8rem var(--theme-400);
}

.title_content h2{
      width: fit-content;
      background: var(--light);
      color: var(--theme-400);
      text-transform: uppercase;
      font-size: 1.8rem;
      position: relative;
      z-index: 2;
      padding: 0 1rem;
}