.content{
      width: 82rem;
      height: 82rem;
      background: var(--theme-600);
      border-radius: 3rem;
      box-shadow: 0 0 10rem rgba(0,0,0,.2);
      position: relative;
}

.content_bg{
      width: 100%;
      height: 100%;
      position: relative;
      background: var(--theme-500);
      border-radius: 3rem;
}

.content_bg__inner{
      width: 100%;
      height: 100%;
      position: relative;
      z-index: 10;
      border-radius: 3rem;
}

.content_bg__layear,
.content_bg__inner:before,
.content_bg__inner:after{
      width: 100%;
      top: 0;
      left: 0;
      border-radius: 3rem;
      box-shadow: 0 3rem 3rem rgba(0,0,0,.2);
}

.content_bg__layear{
      height: 20.5rem;
      background: var(--theme-600);
      position: absolute;
      z-index: 2;
}

.content_bg__inner:before{
      height: 41rem;
      background: var(--theme-100);
      z-index: 1;
}

.content_bg__inner:after{
      height: 61.5rem;
      background: var(--theme-300);
}

.template{
      width: 76rem;
      height: 100%;
      background: var(--light);
      border-radius: 3rem;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 10;
      display: flex;
}