.social_content{
      margin-top: 2rem;
}

.social_menu{
      margin-top: 2rem;
      height: 4.2rem;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 1rem;
}

.btn_social{
      width: 4.2rem;
      height: 4.2rem;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--theme-400);
      color: var(--light);
      border-radius: 50%;
}

.btn_social *{
      font-size: 1.8rem;
}