.friend_slider{
      margin-top: 2rem;
}

.friend_card,
.friend_card a,
.friend_card a img{
      width: 12rem;
      height: 18rem;
      border-radius: .4rem;
      position: relative;
}

.friend_card img{
      filter: grayscale(1);
      transition: .2s;
}

.friend_card:hover a img{
      filter: grayscale(0);
}

.friend_card{
      background: var(--alpha);
      overflow: hidden;
}

.friend_card a strong{
      display: block;
      width: 100%;
      position: absolute;
      text-align: center;
      color: var(--theme-100);
      text-transform: uppercase;
      left: 0;
      bottom: 2rem;
      z-index: 2;
      font-size: 1.6rem;
      transition: .2s;
}

.friend_card:after{
      width: 200%;
      height: 80%;
      transform: translate(-50%, 50%) rotate(-15deg);
      background: var(--secundary);
      left: 50%;
      top: 100%;
      transition: .3s;
}

.friend_card:hover a strong{
      bottom: 2.5rem;
      color: var(--light);
}

.friend_card:hover:after{
      top: 50%;
      transform: translate(-50%, 20%) rotate(-15deg);
}

.slick-list{
      height: 18rem;
}

.slick-slide{
      margin: 0 .5rem;
}