.modal_content{
      position: fixed;
      top: 0;
      left: 0;
      background: rgba(0,0,0,.6);
      display: flex;
      align-items: center;
      justify-content: center;
      opacity: 1;
      visibility: visible;
      transition: .2s;
      width: 100%;
      height: 100vh;
      z-index: 999;
}

.modal_content.show{
      opacity: 1;
      visibility: visible;
}

.close_modal{
      width: 4.2rem;
      height: 4.2rem;
      background: rgba(0,0,0,.8);
      position: absolute;
      top: 2rem;
      right: 2rem;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
}

.close_modal *{
      color: var(--light);
      font-size: 2rem;
}

.modal_inner{
      width: 90%;
      height: 90vh;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      transform: scale(0);
      opacity: 0;
}

.modal_inner img{
      width: initial;
}

.modal_content.show .modal_inner{
      animation: zoom .3s ease-in-out forwards;
}

@keyframes zoom{
      0%{
            transform: scale(1.6);
            opacity: 0;
      }
      80%{
            transform: scale(.6);
            opacity: 1;
      }
      100%{
            transform: scale(1);
            opacity: 1;
      }
}