@import url('https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@100;200;300;400;500;600;700;800&family=Chivo+Mono:wght@100;200;300;400;500;600&family=Oswald:wght@200;400;500;600;700&display=swap');

:root{
  /** fonts */
  --ft-1: 'Oswald', sans-serif;
  --ft-2: 'Big Shoulders Display', cursive;
  --ft-3: 'Chico Mono', monospace;

  /** colors */
  --green: #45d985;
  --light: #fff;
  --red-200: #d11e22;
  --red-100: #e90d0f;
  --alpha: rgba(0,0,0,.05 );
  --grys-300: #b5b5b5;
  --grys-200: #c9d8db;
  --grys-100: #fcffff;

  /** default theme */
  --theme-600: #3c096c;
  --theme-500: #450d7b;
  --theme-400: #5a189a;
  --theme-300: #7b2cbf;
  --theme-200: #9d4edd;
  --theme-100: #cb8dfe;
  --secundary: #ffd403;
  --dark-200: #1f2024;
  --dark-100: #25282d;
  --gradient-90: linear-gradient(90deg, var(--red-100), var(--red-200));
  --gradient-45: linear-gradient(45deg, var(--grys-100), var(--grys-300));
  --gradient: linear-gradient(var(--secundary), var(--theme-300));

  /** social color*/
  --facebook: #3b5995;
  --twitter: #1da1f2;
  --google: #dd4b39;
  --youtube: #c4302b;
  --vimeo: #1ab7ea;
  --dribbble: #ea4c89;
  --github: #333;
  --instagram: #c13584;
  --pinterest: #e60023;
  --flickr: #0063dc;
  --bitbucket: #205081;
  --linkedin: #0e76a8;
  --whatsapp: #12af0a;
  --tumblr: #35465c;
  --tiktok: #222;
  --discord: #5865f2;
  
  /** globals */
  font-family: var(--ft-3);
  font-size: 62.5%;
  color: var(--light);

}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  min-height: 98rem;
  overflow-x: hidden;  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, var(--secundary) 20%, 
  var(--theme-600) 20.05%, var(--theme-600) 45%, var(--dark-200) 45.05%, var(--dark-200) 55%, var(--theme-600) 55.05%, var(--theme-600) 80%, var(--secundary) 80.05%);
}

body *{
  font-size: 1rem;
}

:before,
:after{
  content: '';
  position: absolute;
}

a, span{
  display: inline-block;
}

a{
  text-decoration: none;
}

ul{
  list-style: none;
}

img{
  width: 100%;
  object-fit: cover;
}

button{
  border: none;
  outline: none;
  cursor: pointer;
  font-family: var(--ft-2);
}

.bg_grad{
  display: inline-block;
  color: transparent;
  background: var(--gradient-45);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
}

::-webkit-scrollbar{
  width: .8rem;
  background: var(--alpha);
  border-radius: .8rem
}

::-webkit-scrollbar-thumb{
  background: var(--theme-400);
  border-radius: .8rem;
}