.aside_content{
      width: 28rem;
      height: 100%;
      background: var(--secundary);
      border-radius: 3rem;
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 5rem 3rem;
      flex-shrink: 0;
}

.aside_user{
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 2rem;
      flex-direction: column;
      position: relative;
      z-index: 10;
}

.user_avatar{
      width: 19rem;
      height: 19rem;
      border-radius: 2rem; 
      padding: 1rem;
      box-shadow: 0 0 0 .5rem var(--theme-400) inset;
      position: relative;
}

.user_avatar:before{
      width: 90%;
      height: 90%;
      border-radius: 2rem;
      top: 2rem;
      left: 2rem;
      border: solid .5rem #000;
      opacity: .3;
      filter: blur(4px);
      z-index: -1;
}

.user_avatar img{
      width: 17rem;
      height: 17rem;
      border-radius: 2rem;
}

.aside_user h1{
      margin-top: 2rem;
      color: var(--theme-500);
      font-size: 3rem;
      font-family: var(--ft-1);
}

.aside_user p{
      font-size: 1.6rem;
}

.navigator{
      width: 100%;
      height: 32rem;
      margin-top: 4rem;
      position: relative;
      z-index: 5;
}

.navigator ul{
      width: 100%;
      height: 100%;
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: center;
      gap: .5rem;
}

.navigator ul:before{
      width: 17rem;
      height: 100%;
      background: var(--secundary);
      top: 0;
      right: 0;
      z-index: -1;
}

.navigator ul:after{
      width: 3rem;
      height: 26rem;
      background: #000;
      border-radius: 50%;
      z-index: -2;
      top: calc(50% - 13rem);
      left: 4rem;
      filter: blur(15px);
      opacity: .2;
}

.navigator ul li{
      width: 100%;
      height: 4.2rem;
      display: flex;
      align-items: center;
      gap: 2rem;
      cursor: pointer;
      padding-left: 1rem;
}

.navigator ul li *{
      font-size: 2.4rem;
      color: var(--light);
      transition: .2s;
}

.navigator ul li:hover *,
.navigator ul li.active *{
      color: var(--theme-400);
}

.navigator ul li a,
.navigator ul li span{
      width: 12rem;
      height: 4.2rem;
      display: flex;
      align-items: center;
      background: var(--alpha);
      border-radius: 0 4.2rem 4.2rem 0;
      padding-left: 2rem;
      font-size: 1.4rem;
      text-transform: uppercase;
      transition: .2s;
      font-weight: 500;
}

.navigator ul li:hover a,
.navigator ul li:hover span,
.navigator ul li.active span{
      background: var(--theme-400);
      color: var(--light);
}

.nav_user{
      width: 100%;
      height: 4.2rem;
      margin-top: 3rem;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 1rem;
}

.nav_user a{
      width: 4.2rem;
      height: 4.2rem;
      border-radius: 50%;
      background: var(--alpha);
      display: flex;
      align-items: center;
      justify-content: center;
      transition: .2s;
}

.nav_user a *{
      font-size: 2rem;
      color: var(--light);
      transition: .2s;
}

.nav_user a:hover{
      background: var(--theme-400);
}