.backgrount_content{
      width: 100%;
      height: 100%;
      position: fixed;
      z-index: -1;
      top: 0;
      left: 0;
      overflow: hidden;
}

.square_shape{
      position: absolute;
      background: none;
}

.square_shape:nth-of-type(1){
      width: 26rem;
      height: 26rem;
      border: solid 7rem var(--light);
      left: 0;
      bottom: 0;
      transform: translate(-40%, 20%) rotate(45deg);
}

.square_shape:nth-of-type(2){
      width: 16rem;
      height: 16rem;
      border: solid 4rem var(--light);
      left: 70%;
      top: 0;
      transform: translate(0%, 200%) rotate(45deg);
}

.circle_shape{
      position: absolute;
      border-radius: 50%;
      background: none;
}

.circle_shape:nth-of-type(3){
      width: 14rem;
      height: 14rem;
      left: 20%;
      top: 40%;
      border: solid 3rem var(--light);
}

.circle_shape:nth-of-type(4){
      width: 44rem;
      height: 44rem;
      right: 0;
      top: 0;
      border: solid 10rem var(--light);
      transform: translate(50%, -40%);
}

.rectangle_shape{
      width: 10rem;
      height: 50rem;
      border-radius: 999px;
      background: var(--theme-400);
      position: absolute;
}

.rectangle_shape:before{
      width: 5rem;
      height: 25rem;
      border-radius: 999px;
      background: var(--theme-200);
      right: -8rem;
      top: 5rem;
}

.rectangle_shape:nth-of-type(5){
      top: 0;
      left: 60%;
      transform: translate(0, -50%) rotate(45deg);
}

.rectangle_shape:nth-of-type(6){
      bottom: 0;
      left: 30%;
      transform: translate(0, 50%) rotate(45deg);
}